<template>
 <div class="content">
   <div class="box">
        <span style="--i:1"><img :src="contentImageOne"></span>
        <span style="--i:2"><img :src="contentImageTwo"></span>
        <span style="--i:3"><img :src="contentImageThree"></span>
        <span style="--i:4"><img :src="contentImageFour"></span>
        <span style="--i:5"><img :src="contentImageFive"></span>
        <span style="--i:6"><img :src="contentImageSix"></span>
        <span style="--i:7"><img :src="contentImageSeven"></span>
        <span style="--i:8"><img :src="workImageTwo"></span>
        <!--<span style="--i:8"><img :src="contentImageFive" alt=""></span>-->
    </div>
 </div>
</template>
<script>
import { mapState } from 'vuex';
export default { 
    computed:{
      ...mapState([
       'contentImageOne',
       'contentImageTwo',
       'contentImageThree',
       'contentImageFour',
       'contentImageFive',
       'contentImageSix',
       'contentImageSeven',
       'workImageTwo',
    ])
   }
}
</script>

<style scoped>
.content{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    width: 100vw;
    background: radial-gradient(#9999,#15c);   
}
img{
   position: absolute;
   width: 100%;
   height: 100%;
   object-fit: cover;
}
.box{
    position: relative;
    width: 200px;
    height: 200px;
    transform-style: preserve-3d;
    animation: animate 20s linear infinite;
}
@keyframes animate{
    0%{
        transform: perspective(1000px) rotateY(0deg);
    }
    100%{
        transform: perspective(1000px) rotateY(360deg);
    }
}
.box span{
    position: absolute;
    width: 100%;
    height: 100%;
    transform-origin: center;
    transform-style: preserve-3d;
    transform:rotateY(calc(var(--i) * 45deg)) translateZ(400px);
    -webkit-box-reflect:below 0px linear-gradient(transparent,transparent,#0004)
}

</style>


