<template>
  <div id="app">
     <div class="fade">
         <Header />
         <div class="content">
           <router-view  />
         </div> 
     </div>
  </div>  
</template>
<script>
import Header from './components/Header.vue';

export default {
  components:{
    Header,
  }
}
</script>
<style >
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,300;1,900&display=swap');
*{
  margin: 0;
  padding: 0;
}
body{
  background: radial-gradient(#9999,#15c);
}
.fade{
  animation: blackcover 3s ease-in-out;
}
@keyframes blackcover{
  0%{ background-color: black;}
  50%{ background-color: rgb(41, 40, 38);}  
}
.image{
  text-align: center;
}
.content{
  width: 100%;
  height: 100vh;
  overflow-x: hidden;
  font-family: 'Poppins', sans-serif;
}
</style>


