<template>
  <div class="container">
      <div id="progress" :style="{ width: progress }"></div><!--1つ入力される毎に、25%ずつ左から右に青色の要素が覆いかぶさってくる。-->
       <h1 :class="{ 'show-final': showFinal }">ご登録ありがとうございます。<!--全て入力された後に表示される。-->
           {{ registerSteps[0].value }} {{ registerSteps[1].value }} 様<!--1回目と2回目に入力された性と名が表示される。-->
       </h1>
       <div id="register">
          <font-awesome-icon  v-if=" position === 0 " icon="user-secret" class="previousButton" />
          <font-awesome-icon v-else icon="angle-left" class="previousButton" @click="previousStep" />
          <font-awesome-icon icon="angle-right" class="forwardButton" @click="checkStep" />
          <div class="inputContainer" :class="{ 'showContainer': showContainer }"><!--最初に表示されるcontainer-->
              <form @submit.prevent="checkStep">
                  <input :type="inputType" class="inputField"
                         ref="registerinput" v-model="inputValue" required>
                  <label id="inputLabel">{{ inputLabel }}</label>
              </form>
              <div class="inputProgress"></div>
          </div>
       </div>
  </div>
</template>
<script>
import { fas } from '@fortawesome/free-solid-svg-icons';
import { library } from '@fortawesome/fontawesome-svg-core';
import { faUserSecret } from '@fortawesome/free-solid-svg-icons';
library.add(faUserSecret,fas)
export default {
   data(){
       return{  
            show:false,
            position: 0,
            inputLabel: '',
            inputType: 'text',
            inputValue: '',
            progress: '0%',
            showFinal: false,
            showContainer: false,
            registerSteps:[
            {   // this.registerSteps[0]
               label: '性を入力して下さい。',
               type: 'text',
               value: '', 
               pattern: /.+/
            },
            {  // this.registerSteps[1]
               label: '名前を入力して下さい。?',
               type: 'text',
               value: '', 
               pattern: /.+/
            },
             { // this.registerSteps[2]
               label: 'emailを入力して下さい。?',
               type: 'text',
               value: '', 
               pattern: /^[^\s@]+@[^\s@]+\.[^\s@]+$/
            },
             { // this.registerSteps[3]
               label: 'passwordを入力して下さい?',
               type: 'text',
               value: '', 
               pattern: /.+/
            }
          ] 
       }
   },
   methods:{
       setStep(){// それぞれに対応するdata同士を,registerStepsの[0]から結びつける。
        this.inputLabel = this.registerSteps[this.position].label 
        this.inputType = this.registerSteps[this.position].type
        this.inputValue = this.registerSteps[this.position].value
        this.$refs.registerinput.focus()// このinputエリアに、ユーザーがmouseを動かさなくても自動的に入力ができる
        this.showStep()
      },
      showStep(){
          setTimeout(()=>{
              this.showContainer = true // inputエリアの表示
          },100)
      },
      previousStep(){
          this.position -= 1 // -1 from register[this.position]
          register.classList = ''
          this.hideStep(this.setStep)
          this.setProgress()
      },
      hideStep(callback){
          this.showContainer = false // inputエリアが消える。
          setTimeout(callback,100) 
      },
      checkStep(){
          if(!this.registerSteps[this.position].pattern.test(this.inputValue)){ // this.inputValueがpatternに合致しない際に、classList = wrongが追加される。　
             register.classList.add('wrong')
             register.classList.add('wrongAnimation')
             setTimeout(()=>{
                 register.classList.remove('wrongAnimation')
             },500)
             this.$refs.registerinput.focus()//このinputエリアに、ユーザーがmouseを動かさなくても自動的に入力ができる
          }
          else{ // this.inputValueがpatternに合致する際に、classList = wellAnimationが追加される。　
             register.classList = ''
             register.classList.add('wellAnimation')
              setTimeout(()=>{
                register.classList.remove('wellAnimation')
             },200)
             this.registerSteps[this.position].value = this.inputValue

             this.position += 1 // this.registerSteps[1]に切り替わる。
             if(this.registerSteps[this.position]){　// this.registerSteps[0] ~ [2]まではこのステップを踏む。
                 this.hideStep(this.setStep)
             }
             else{
                 this.hideStep(()=>{　// this.registerSteps[3]の場合、次のObjectが存在しないため、hideStepが起動される。
                     register.className = 'close'
                     setTimeout(()=>{
                     this.showFinal = true
                     },1000)
                 })
             }
          }
          this.setProgress()
      },
      setProgress(){
          this.progress = (this.position / this.registerSteps.length * 100) + '%' // 25%毎にprogressが増える。
      },
      
   },
    mounted(){
        const register = document.getElementById('register')
        this.setStep()　// setupが起動される
    }
}
</script>
<style scoped>

.forwardButton{
    font-size: 20px;
}
.container{
    position: relative;
    width: 100%;
    height: 100vh;
    font-family: 'Noto Sans', sans-serif;
    font-size: 10px;
    color: #333;
    display: flex;
    justify-content: center;
    align-items: center;
    background: radial-gradient(#9999,#15c);
}
#progress{
    position: absolute;
    top: 0;
    left: 0;
    width: 0%;
    height: 100vh;
    background:linear-gradient(rgb(4, 4, 245),rgb(4, 4, 53)) ;
    transition: width 1s ease-in-out;
}
#register{
    position: absolute;
    width: 400px;
    height: 90px;
    background-color: aliceblue;
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.2),
                0 10px 10px rgba(0, 0, 0, 0.2);
}
.previousButton{
    position: absolute;
    left: 30px;
    top: 10px;
    font-size: 15px;
    color: #9e9e9e;
    cursor: pointer;
    z-index: 20;
}
.previousButton:hover{
    color: #15c;
}
.forwardButton{
    position: absolute;
    right: 30px;
    top: 30px;
    font-size: 30px;
    color: #15c;
    cursor: pointer;
    z-index: 20;
}
.forwardButton:hover{
    color: #15c;
}
.inputContainer{
    position: relative;
    padding: 30px 20px 20px 20px;
    margin-right: 60px;
    opacity: 0;
    transition: opacity .3s ease-in-out;
}
input{
    width: 100%;
    font-size: 19px;
    font-weight: bold;
    outline: none;
    background: transparent;
    box-shadow: none;
    border: none;
}
:valid + #inputLabel{
    top: 10px;
    left: 50px;
    font-size: 10px;
} 
#inputLabel{
    position: absolute;
    top: 25px;
    left: 22px;
    font-size: 20px;
    pointer-events: none;
    transition: .2s ease-in-out;
}
.inputProgress{
    width: 0%;
    border-bottom: 6px solid rgb(47, 47, 145);
    transition: width .2s ease-in-out;
}
.showContainer{
    opacity: 1 !important;
}
.showContainer .inputProgress{
    width: 100%;
}
.wrong .forwardButton{
    color: #D93F38;
}
.close .forwardButton, .close .previousButton{
    color: #fff;
}
.wrong .inputProgress{
    border-color: #D93F38;
}
.wrongAnimation{
    animation: .5s linear 0s 1 wrong-animation;
}
.wellAnimation{
    animation: .2s linear 0s 1 well-animation;
}
@keyframes wrong-animation{
    0%{ transform: translateX(0);}
    20%{ transform: translateX(-20px);}
    40%{ transform: translateX(20px);}
    60%{ transform: translateX(-20px);}
    80%{ transform: translateX(20px);}
    100%{ transform: translateX(0);}
}
@keyframes well-animation{
    0%{ transform: translateY(0);}
    100%{ transform: translateY(-20px);} 
}
.close .forwardButton, .close .previousButton{ 
   color: #fff;
}
.close{
    width: 0%;
    padding: 10px 0;
    overflow:hidden;
    transition: .8s ease-in-out;
    box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.2);
    opacity: 0;
}
h1{
    position: absolute;
    width: max-content;
    font-size:30px;
    color:#fff;
    opacity: 0;
    transition: .8s ease-in-out;
}
.show-final{
    opacity: 1;
}
.changeColor{
    font-size: 20px;
    color: blue;
}
</style>


