<template>
    <div class="slide">
      <div class="text">
        <h2>{{ theme }}</h2>
        <transition name="myself">
           <div>
               <img :src="contentImageOne" :style="{width:'50%',height:'300px'}">
           </div>   
        </transition>
      </div>
    </div> 
</template>
<script>
import store from '../store.js'
import { mapState } from 'vuex'
export default {
  data(){
    return{
      show : true,
      showTwo: false
    }
  },
  computed:{
    ...mapState([
          'theme',
          'contentImageOne',
          'contentImageTwo'
    ])
  },
  
}
</script>
<style scoped>
*{
    margin: 0;
    padding: 0;
}
.slide{
    width: 100%;
    height: 100vh;
    overflow-x: hidden;
    display: block;
    position: relative;
    background-blend-mode:lighten;
}
.text{
  min-height: 100%;
  background-image: url('../assets/rus3.png');
  background-position: center no-repeat;
  background-blend-mode:lighten;
  position: relative;
  z-index: 0;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.text:before{
  content: '';
  background: inherit;
  -webkit-filter: blur(5px);
  -moz-filter: blur(5px);
  -o-filter: blur(5px);
  -ms-filter: blur(5px);
  filter: blur(5px);
  position: absolute;
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
  z-index: -1;
}
h2{
  text-align: center;
  padding-top: 30px;
  font-size: 50px;
  letter-spacing: 5px;
  display: block;
}
img{
    width: 100px;
    margin-left: 10%;
    margin-top: 50px;
}
.slide{
  animation: animate 5s ease-in-out;
}
@keyframes animate{
  0%{
    opacity: 0;
  }
  50%{
    opacity: 0.5;
    
  }
  100%{
     opacity: 1;
  }
}
.myself-enter-active{
  transition: all 1s ease;
}
.myself-leave-active {
  transform: translateX(100%);
  transition: all 2s ease-in-out;
  opacity: 0;
}
.myself-enter{
  transform: translateX(50%);
  opacity: 0;
}
@media screen and (max-width: 481px) {
  h2{
    font-size: 20px;
  }
}

</style>


