<template>
  <section>
    <div class="content">
      <h3>Validation Checker</h3>
      <form id="form">
        <div class="inputbox">
          <input type="text" id="email" placeholder="Enter Email Adress">
          <button @click="validation">確認</button>
        </div>
        <span id="text"></span>
      </form>
    </div>
  </section>
</template>

<script>
export default {
  methods:{
     validation(){
          const form = document.getElementById('form');
          const email = document.getElementById('email').value;
          const text = document.getElementById('text');
          const pattern = /^[^ ]+@[^ ]+\.[a-z]{2,3}$/; //正規表現。
  
          if(email.match(pattern)){
              form.classList.add('valid')
              form.classList.remove('invalid')
              text.innerHTML = 'このEメールアドレスは使用出来ます。'
              text.style.color = "#00ff00"
              text.style.fontSize = '20px'
          }else{
              form.classList.remove('invalid')
              form.classList.add('invalid')
              text.innerHTML = 'このEメールアドレスは使用出来ません。'
              text.style.color = "#ff0000"
              text.style.fontSize = '20px'
          }
      }
  }
}
</script>

<style scoped>
*{
    margin: 0;
    padding: 0;
}
section{
    min-height: 100vh;
    width: 100vw; 
}
.content{
    display:flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    width: 100vw;
    background: radial-gradient(#9999,#15c);
}
h3{
    position: relative;
    font-size: 20px;
    font-weight: 500;
    color:rgb(19, 13, 13);
    letter-spacing: 1px;
    margin-bottom: 3px;
    padding: 10px;
}
#form{
    position: relative;
}

#form #email{
    outline: none;
    border: none;
    width: 300px;
    background: #292929;
    padding: 10px;
    color: aliceblue;
    border-radius: 10px;
}
</style>


