<template>
  <div class="introduceMyself">
    <div class="container">
          <div class="tabs" :style="{ cursor:'pointer' }"><!--それぞれのaはactivetab値をもつので、クリックされたaがactiveのclassが追加される。-->
            <a @click=" activetab=1 " :class="[ activetab === 1 ? 'active' : '' ]">自己紹介</a>
            <a @click=" activetab=2 " :class="[ activetab === 2 ? 'active' : '' ]">経歴</a>
            <a @click=" activetab=3 " :class="[ activetab === 3 ? 'active' : '' ]">スキル</a>
            <a @click=" activetab=4 " :class="[ activetab === 4 ? 'active' : '' ]">趣味</a>
            <a @click=" activetab=5 " :class="[ activetab === 5 ? 'active' : '' ]">好きな旅行先</a>
            <a @click=" activetab=6 " :class="[ activetab === 6 ? 'active' : '' ]">資格</a>
         </div>    
      　　<div class="content"><!--自己紹介の内容部分-->  
　　　　　　　<div v-show=" activetab === 1 " class="tabcontent" :style="{ overflow:'scrollX' }"><!--activetab = 1の時のみ表示 -->
              <ul><!--名前と生年月日-->
                <li>金子恵太</li>
                <li>1996年2月7日生まれ</li>
                <li>24歳</li>
              </ul>
            </div>
            <div v-show=" activetab === 2 " class="tabcontent"><!--activetab = 2の時のみ表示 -->
              <ul><!--経歴-->
                <li>2014年4月1日 日本大学法学部入学</li>
                <li>2018年3月31日 日本大学法学部卒業</li>
                <li>2018年2月 ~ 現在まで無職(顔面神経麻痺が原因で,2019年12月まで療養中でした。)</li>
                <li>2020年1月 ~ IT業界の勉強を開始</li>
                <li>現在　就職活動中</li>
              </ul>
            </div>
             <div v-show=" activetab === 3 " class="tabcontent"><!--activetab = 3の時のみ表示 -->
               <ul><!--スキル-->
                 <li>HTML(実務経験無し)</li>
                 <li>CSS(実務経験無し)</li>
                 <li>JavaScript(実務経験無し)</li>
                 <li>Vue.js(実務経験無し)</li>
                 <li>Python(実務経験無し)</li>
                 <li>Docker(実務経験無し)</li>
               </ul>
             </div>
            <div v-show=" activetab === 4 " class="tabcontent"><!--activetab = 4の時のみ表示 -->
                 <ul><!--趣味-->
                   <li>旅行</li>
                   <li>食べ歩き</li>
                   <li>サッカー観戦</li>
                   <li>語学勉強</li>
                   <li>散歩</li>
                   <li>筋肉トレーニング</li>
                 </ul>
            </div>
            <div v-show=" activetab === 5 " class="tabcontent"><!--activetab = 5の時のみ表示 -->
              <ul><!--お気に入りの旅行先-->
                <li>ナポリ</li>
                <li>ローマ</li>
                <li>ヴェネツィア</li>
                <li>バルセロナ</li>
                <li>ウラジオストク</li>
                <li>北海道</li>
              </ul>
            </div>
             <div v-show=" activetab === 6 " class="tabcontent"><!--activetab = 6の時のみ表示 -->
              <ul><!--保有資格-->
                <li>2019年3月 TOEIC公開テスト 800点取得</li>
                <li>2018年7月普通自動車第一種運転免許（AT限定)</li>
              </ul>
             </div>
      　　</div>
    </div>
  </div>
</template>

<script>

export default {
　data(){
   return{
      activetab: 1  /* activetabの初期値は１なので、自己紹介の部分が必ず最初に表示される。*/
    }  
  },
}
</script>

<style scoped>
.introduceMyself{
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  background: radial-gradient(#9999,#15c);
}
.container{
  padding-top: 100px;
}
.tabs{
  font-size: 20px;
  background: radial-gradient(#9999,#15c);
}
.tabs a{
  color: aliceblue;
  padding:0 15px;
  border-radius: 10px 10px 0 0;
  cursor: pointer;
}
.content{
  height: 50vh;
  font-size: 20px;
  padding: 20px 10px;
  border: 2px solid black;
  box-shadow:  -5px -5px 10px  rgb(58, 56, 56) inset;
  transition: all 2s ease-in-out;
  background-color: aliceblue;
  overflow-x: scroll;
}
.tabcontent{
  min-height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(72, 72, 87);
  animation: animate 1s ease-in-out;
}
li{
  padding: 10px;
  list-style: none;
  border-bottom: 1px solid blue; 
}
@keyframes animate{
  0%{
    transform: translateY(-100%);
    opacity: 0;
  }
  100%{
    transform: translateY(0%);
    opacity: 1;
  }
}
.active{
  background-color: blue;
  color: white;
  transition: all 1s ease-in-out;
}
</style>


