import Content from './components/Content.vue';
import ContentTwo from './components/ContentTwo.vue';
import ContentThree from './components/ContentThree.vue';
import ContentFour from './components/ContentFour.vue';
import work1Home from './components/Work/work1/work1Home.vue';
import work2Home from './components/Work/work2/work2Home.vue';
import work3Home from './components/Work/work3/work3Home.vue';
import work4Home from './components/Work/work4/work4Home.vue';
import work5Home from './components/Work/work5/work5Home.vue';



export const routes = [
    {path:'/',component:Content},
    {path:'/Two',component:ContentTwo},
    {path:'/Three',component:ContentThree},
    {path:'/Four',component:ContentFour},
    {path:'/work1Home',component:work1Home,name:'1'},
    {path:'/work2Home',component:work2Home,name:'2'},
    {path:'/work3Home',component:work3Home,name:'3'},
    {path:'/work4Home',component:work4Home,name:'4'},
    {path:'/work5Home',component:work5Home,name:'5'},
]


