<template>
  <div class="row">
    <div class="green">
        <p class="greenText">Vue.js</p>
        <h4>35%</h4>
    </div>
    <div class="smokePink">
        <p class="smokePinkText">Pure Javascript</p>
        <h4>40%</h4>
    </div>
    <div class="blue">
        <p class="blueText">English</p>
        <h4>25%</h4>    
        
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
export default {
  computed:{
      ...mapState([
          'squareColor'
      ])
  }
}
</script>
<style scoped>
.row{
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    color: rgb(248, 240, 240);
    margin-top: 30px;
    animation: animate 3s ease-in-out;
}
.green{
    background: #06940d;
    width: 100px;
    height: 100px;
}
.smokePink{
    background: #e8a18b;
    width: 100px;
    height: 100px;
}
.blue{
    background: #021c42;
    width: 100px;
    height: 100px;
}
.greenText{
    text-align: center;
    margin-top: 35px;
    font-size: 20px;
}
.smokePinkText{
    text-align: center;
    margin-top: 10px;
    font-size: 20px;
}
.blueText{
    text-align: center;
    margin-top: 35px;
    font-size: 20px;
}
@keyframes animate{
    0%{
        transform: rotateX(180deg);
    }
    100%{
        transform: rotateX(0deg)
    }
}
</style>


