<template>
  <div class="work">
    <h1>作品集<small :style="{　fontSize:'25px'　}">(作品は随時更新していきます。)</small></h1>
    <div class="container">
         <div :class="{　'up':up　}" class="eachContainer"
              v-for="(work,i) in works" :key="i"
              @mouseover="moveup"><!-- mouseを置くとup = trueになり、aroundのclassが付与されることで、Y軸に10%上がる。--> 
              <div>
                　<div class="title">
                    {{　work.title　}}
                　</div>
                　<div class="workImage">
                    <img :src="work.src" :style="{　width:'100%', height:'100px'　}">
                　</div>
                　<div class="description">
                    <p>{{　work.description　}}</p>
                　</div>
                　<div class="links">
                  　　<router-link :to="{　name:work.id　}"><!--クリックすると、それぞれのリンクに飛ぶ。-->
                    　　<button class="viewButton">見る</button>
                  　　</router-link>
                　</div>
           　　</div>
         </div>
    </div>    
  </div>
</template>
<script>
import { mapState } from 'vuex';

export default {
  data(){
    return{
      up: false,
    }   
  },
  methods:{
    moveup(i){
      this.up = true
    }
  },
  computed:{
       ...mapState([
         'works'
       ])
  }
}
</script>
<style scoped>
.work{
  height: 100%;
  width: 100%;
}
h1{
  color: aliceblue;
  margin-left: 50px;
}
small{
  color: red;
}
.container{
  margin: 0 auto;
}
.eachContainer{
  border: 1px solid black;
  height: 300px;
  width: 300px;
  margin: 20px;
  border-radius: 10%;
  overflow: scroll;
  transition: all 1s ease-in-out;
}
.up{
  border: 1px solid black;
  height: 30vh;
  width: 30%;
  margin: 20px;
  display: inline-block;
  border-radius: 10%;
  overflow: scroll;
  transition: all 1s ease-in-out;
}
.up:hover{
  transform:translateY(-10%)
}
.title{
  text-align: center;
  border-bottom: 1px solid black;
  background-color: rgb(236, 231, 231);
  font-size: 18px;
}
.description{
  font-weight: 700;
  font-size: 20px;
}
.links{
  text-align: center;
}
button{
  cursor: pointer;
}
.viewButton{
  padding:  15px;
}
</style>


