<template>
  <div class="row" :style="pieStyle"></div>
</template>
<script>
import { mapState } from 'vuex';
export default {
  computed:{
    pieStyle(){
      let sum = 0
      let styles = this.pieData.map(
          piePart => `${piePart.color} 0 ${sum + piePart.value}%`
      )  
      return {background: 'conic-gradient('+ styles.join(",")+')'}
    },
    ...mapState([
      'pieData'
    ]) 
  }
}
</script>

<style scoped>
.row{
    width: 300px;
    height: 300px;
    border-radius: 50%;
    border: 2px solid #fefefe;
    animation: animate 1s ease-in-out;
    margin: auto;
    display: block;
}
@keyframes animate{
    0%{
        transform:scale(0) rotate(900deg);
    }
    100%{
        transform:scale(1) rotate(0deg);
    }
}
</style>


